import React, { useContext } from 'react';
import AppContext from '../Context/AppContext';

import Tab from './Tab';

import 'bootstrap/dist/css/bootstrap.min.css';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

function Tab1() {
  const { setKey } = useContext(AppContext);

  const nextPage = () => {
    setKey('attributes');
  };

  return (
    <Tab>
      <h1 className="text-center mt-4 mb-4">Welcome to Token Creator Wizard</h1>
      <Row className="justify-content-md-center">
        <Form.Group
          as={Row}
          className="text-center mt-2 mb-2"
          style={{paddingTop: '20px', width: '100vw'}}
        >
          <Col md="6">
            <Form.Check
              defaultChecked
              column="true"
              id="token-type"
              label="Fixed Supply ERC-20"
              name="token"
              required
              style={{paddingBottom: '20px', paddingTop: '15px'}}
              type="radio"
              value="token"
            />
          </Col>
          <Form.Text
            style={{paddingBottom: '20px', paddingTop: '15px'}}
          >
            Total supply is created on contract deployment, making the supply fixed.
          </Form.Text>
        </Form.Group>
      </Row>

      <Form.Group as={Row} inline="true" className="justify-content-center">
        <Col xs="auto" sm={{ span: 10, offset: 5 }} className="text-center mt-4 mb-4">
          <Button variant="info" type="button" onClick={ nextPage }>Next</Button>
        </Col>
      </Form.Group>
    </Tab>
  )
}

export default Tab1;
