import Joi from '@hapi/joi';

export const MAX_SUPPLY = 1000000000000000;

const SETUP = {
  name: Joi.string().min(3).max(25).required(),
  symbol: Joi.string().min(3).max(4).required(),
  supply: Joi.number().integer().min(1).max(MAX_SUPPLY).required(),
};

export const SETUP_SCHEMA = Joi.object(SETUP);

export const CREATE_SCHEMA = Joi.object({
  owner: Joi.string().regex(/^0x[a-fA-F0-9]{40}$/).required(),
  ...SETUP,
});

export const validate = (schema) => (data) => {
  const { error } = schema.validate(data);
  if (error) throw new Error(error.details[0].message);
};
