import React, { useContext } from 'react';
import { Tabs } from 'react-bootstrap';
import { Tab } from 'bootstrap';
import { Card } from 'react-bootstrap'
import AppContext from './Context/AppContext';
import Header from './Pages/Header';
import Tab1 from './Pages/Tab1';
import Tab2 from './Pages/Tab2';
import Tab3 from './Pages/Tab3';
import Tab4 from './Pages/Tab4';
import Tab5 from './Pages/Tab5';
import 'bootstrap/dist/css/bootstrap.min.css';

const tabStyle = {
  margin: '0px auto',
  width: '100%',
  paddingTop: '20px',
  paddingBottom: '20px',
  color: '#f79007',
};

function App() {
  const { key, setKey } = useContext(AppContext);
  
  return (
    <Card style={{width: '80%', margin: '50px auto', paddingBottom: '10%'}}>
      <Card.Header>
        <Header />
      </Card.Header>
      <Tabs
        id="controlled-tab-example"
        activeKey={key}
        onSelect={(k) => setKey(k)}
        className="justify-content-center"
        variant="pills"
        style={tabStyle}
      >
        <Tab eventKey="token" title="Token Type" disabled>
          {
            (key === 'token')
              ? <Tab1 onSelect={(k) => setKey(k)} style={{backgroundColor: '#f79007'}}/>
              : <div />
          }
        </Tab>
        <Tab eventKey="attributes" title="Token Attributes" disabled>
          {
            (key === 'attributes')
              ? <Tab2 onSelect={(k) => setKey(k)}/>
              : <div />
          }
        </Tab>
        <Tab eventKey="payment" title="Payment Details" disabled>
          {
            (key === 'payment')
              ? <Tab3 onSelect={(k) => setKey(k)}/>
              : <div />
          }
        </Tab>
        <Tab eventKey="confirmation" title="Confirmation" disabled>
          {
            (key === 'confirmation')
              ? <Tab4 onSelect={(k) => setKey(k)}/>
              : <div />
          }
        </Tab>
        <Tab eventKey="loading" title="Deploying" disabled>
          {
            (key === 'loading')
              ? <Tab5 onSelect={(k) => setKey(k)} />
              : <div />
          }
        </Tab>
      </Tabs>
    </Card>
  );
}

export default App;
