import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Form from 'react-bootstrap/Form';

const formStyle = {
  border: '1px solid #f79007',
  borderRadius: '5px',
  marginRight: 'auto',
  marginLeft: 'auto',
  width: '70%',
  paddingTop: '20px',
  textAlign: 'center',
};

function Tab ({ children, style }) {
  return (
    <Form
      style={{ ...formStyle, ...style}}
    >
      { children }
      <small>v1.0a</small>
    </Form>
  )
}

export default Tab;
