import React from 'react';
import logo from '../images/reibitCripto.png';

function Header() {
  return (
    <div
      style={{
        width: '100%',
        height: '120px',
        backgroundColor: '#363636',
      }}
    >
      <div style={{ width: '1000px', height: '100%', display: 'block', margin: '0 auto' }}>
        <div style={{ height: '100%', display: 'table', float: 'left', margin: '0 350px' }}>
          <h1
            style={{
              color: '#f79007',
              height: '100%',
              display: 'table-cell',
              verticalAlign: 'middle',
              fontSize: '30px',
            }}
          >
            REI DO BITCOIN
          </h1>
        </div>
        <div style={{ float: 'right', height: '100%' }}>
          <div style={{ height: '100%', display: 'table', float: 'left', padding: '0px 20px', margin: '0 -400px' }}>
            <img
              src={logo}
              alt="Reibit-Cripto"
              width="100px"
              style={{ display: 'table-cell', verticalAlign: 'middle', height: '82%', paddingTop: '6px' }}
            />
          </div>
        </div>
      </div>
    </div>
  );
}

export default Header;
