import React, { useContext, useEffect, useState } from 'react';
import AppContext from '../Context/AppContext';

import { deploy } from '../Services/api';

import Tab from './Tab'

const tabStyle = {
  display: 'flex',
  minHeight: '50vh',
  flexDirection: 'column',
  justifyContent: 'space-around',
  alignItems: 'center',
}

function Tab5() {
  const { jwt, setKey } = useContext(AppContext);

  const [contractAddr, setContractAddr] = useState(null);
  const [hash, setHash] = useState(null);

  const startDeploy = async () => {
    try {
      const { address, hash } = await deploy(jwt);
      setContractAddr(address);
      setHash(hash);
    } catch {
      console.error('Invalid request');
      setKey('token');
    }
  };

  useEffect(() => {
    if (jwt) startDeploy();
  }, [jwt]);

  return (
    <Tab style={ tabStyle }>
      <div>
        {
          !(contractAddr && hash)
            ? <h2> Your contract is being deployed...</h2>
            : <h2> Success! Your contract has been deployed </h2>
        }
      </div>
      {
        !(contractAddr && hash) ? (
          <div style={{ textAlign: 'center' }}>
            <h3>DO NOT REFRESH OR LEAVE THIS PAGE!</h3>
            <p>Refreshing or leaving the page will invalid contract deployment</p>
            <div className="spinner-border" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        ) : (
          <div style={{ textAlign: 'center' }}>
            <h3>Contract information</h3>
            <p>Use these informations to interact with your contract</p>
            <p>
              <a
                href={`https://ropsten.etherscan.io/address/${contractAddr}`}
                className="btn btn-info"
                rel="noreferrer"
                target="_blank"
                style={{ marginRight: '8px' }}
              >
                Contract Address
              </a>
              <a
                href={`https://ropsten.etherscan.io/tx/${hash}`}
                rel="noreferrer"
                target="_blank"
                className="btn btn-info"
                style={{ marginRight: '8px' }}
              >
                Hash
              </a>
              <button
                type="button"
                className="btn btn-success"
                style={{ marginRight: '8px' }}
              >
                Download Contract
              </button>
            </p>
          </div>
        )
      }
    </Tab>
  )
}

export default Tab5
