import React, { useContext } from 'react';
import AppContext from '../Context/AppContext';
import { deployToken } from '../Services/api';
import { MAX_SUPPLY } from '../Services/validate';

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Tab from './Tab'

const inputLabel = {
  paddingBottom: '20px',
  paddingTop: '15px',
};

const inputStyle = {
  borderBottom: '1px solid #f79007',
  borderTop: 'none',
  paddingTop: '10px',
  paddingLeft: '20px',
};

const formTextAlign = {
  paddingBottom: '30px',
  textAlign: 'left',
};

function Tab4() {
  const { setKey, tokenData, jwt, setJwt } = useContext(AppContext);

  const finishPage = async () => {
    try{
      const data = await deployToken(tokenData, jwt);
      setJwt(data.token);
      setKey('loading');
    } catch (err) {
      alert(err.message);
    }
  };

  return (
    <Tab>
      <h2 className="text-center mt-4 mb-4">Contract Parameters:</h2>
      <Form.Group as={Row} className="text-center mt-4 mb-4">

      <Form.Label column="true" md={5} htmlFor="name" style={inputLabel}>Token Name</Form.Label>
        <Col md={6}>
          <Form.Control
            readOnly
            style={inputStyle}
            plaintext type="text"
            value={tokenData.name}
            id="name"
            name="tname"
            label="Token name"
          />
            <Form.Text style={formTextAlign}>
              The name of the token, 3-25 symbols. Alphanumerical characters, space, and hyphen are accepted.
            </Form.Text>
        </Col>

        <Form.Label column="true" md={5} htmlFor="symbol" style={inputLabel}>Token Symbol</Form.Label>
        <Col md={6}>
          <Form.Control readOnly style={inputStyle} plaintext type="text" id="symbol" value={tokenData.symbol} />
            <Form.Text style={formTextAlign}>3-4 characters (example ETH, BTC, BAT, etc.). No spaces. Only alphanumerical characters.</Form.Text>
        </Col>
        
        <Form.Label column="true" md={5} htmlFor="supply" style={inputLabel}>Total Supply</Form.Label>
        <Col md={6}>
          <Form.Control readOnly style={inputStyle} plaintext type="text" id="supply" value={tokenData.supply} />
            <Form.Text style={formTextAlign}>Total amount of tokens to be generated. Minimum value is 1, and maximum { MAX_SUPPLY }.</Form.Text>
        </Col>

        <Form.Label column="true" md={5} htmlFor="owner" style={inputLabel}>Owner</Form.Label>
        <Col md={6}>
          <Form.Control readOnly style={inputStyle} plaintext type="text" id="owner" value={tokenData.owner} />
            <Form.Text style={formTextAlign}>Owner Address</Form.Text>
        </Col>

        <Col sm={{ span: 6, offset: 3 }} className="text-center mt-4 mb-4">
          <Button variant="success" type="button" name="finish" onClick={ finishPage }>Deploy!</Button>
        </Col>
        
      </Form.Group>
    </Tab>
  )
}

export default Tab4;
