import React, { useState, useEffect, useContext } from 'react';
import { MAX_SUPPLY, SETUP_SCHEMA, validate } from '../Services/validate';
import AppContext from '../Context/AppContext';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Footer from '../Components/Footer';

import Tab from './Tab'

const inputLabel = {
  paddingBottom: '20px',
  paddingTop: '15px',
};

const inputStyle = {
  borderBottom: '1px solid #f79007',
  borderTop: 'none',
  paddingTop: '10px',
  paddingLeft: '20px',
};

const formTextAlign = {
  paddingBottom: '30px',
  textAlign: 'left',
};

function Tab2() {
  const { tokenData, setTokenData } = useContext(AppContext);
  const [input, setInput] = useState(tokenData);
  const [isVerified, setIsVerified] = useState(false);

  useEffect(() => {
    setTokenData(input);
    try {
      validate(SETUP_SCHEMA)(input);
      setIsVerified(true);
    } catch {
      setIsVerified(false);
    }
  }, [input, setTokenData]);

  const handleChange = ({ target }) => {
    setInput({...input, [target.name]: target.value });
  }

  return (
    <Tab>
      <Form.Group as={Row} className="text-center md-4" style={{paddingTop: '20px'}}>
        <Form.Label column="true" md={5} htmlFor="name" style={inputLabel}>Token Name</Form.Label>
        <Col md={6}>
          <Form.Control
            style={inputStyle}
            plaintext
            type="text"
            placeholder="Token name*"
            id="name"
            name="name"
            label="Token name"
            onChange={ handleChange }
            value={ tokenData.name }
          />
            <Form.Text id="nameHelpBlock" style={formTextAlign}>
              The name of the token, 3-25 symbols. Alphanumerical characters, space, and hyphen are accepted.
            </Form.Text>
        </Col>

        <Form.Label column="true" md={5} htmlFor="symbol" style={inputLabel}>Token Symbol</Form.Label>
        <Col md={6}>
          <Form.Control
            style={inputStyle}
            plaintext
            type="text"
            placeholder="Token symbol*"
            id="symbol"
            name="symbol"
            onChange={ handleChange }
            value={tokenData.symbol}
          />
            <Form.Text style={formTextAlign}>3-4 characters (example ETH, BTC, BAT, etc.). No spaces. Only alphanumerical characters.</Form.Text>
        </Col>
        
        <Form.Label column="true" md={5} htmlFor="supply" style={inputLabel}>Total Supply</Form.Label>
        <Col md={6}>
          <Form.Control
            style={inputStyle}
            plaintext
            type="number"
            placeholder="Total supply*"
            id="supply"
            name="supply"
            min={1}
            max={MAX_SUPPLY}
            step={1}
            onChange={ handleChange }
            value={ tokenData.supply }
          />
            <Form.Text style={formTextAlign}>Total amount of tokens to be generated. Minimum value is 1, and maximum { MAX_SUPPLY }.</Form.Text>
        </Col>
      </Form.Group>
      <Footer from="token" to="payment" avaiable={isVerified} />
    </Tab>
  )
}

export default Tab2;
