import React, { useContext } from 'react';
import Button from 'react-bootstrap/Button';
import AppContext from '../Context/AppContext';

const style = {
  width: '100%',
  display: 'flex',
  justifyContent: 'space-around',
};

const Footer = ({ to, from, avaiable }) => {
  const { setKey } = useContext(AppContext);
  const back = () => {
    setKey(from);
  };

  const submit = async () => {
    setKey(to);
  };
  return (
    <div style={style} >
      { 
        from && <Button variant="danger" column="true" type="button" onClick={ back }>Back</Button>
      }
      <Button
        variant="info"
        column="true"
        type="button"
        disabled={ !avaiable }
        onClick={ submit }
      >
        Next
      </Button>
    </div>
  );
}

export default Footer;
